.doc .dlist .olist,
.doc .dlist .ulist,
.doc .olist .dlist,
.doc .olist .olist,
.doc .olist .ulist,
.doc .ulist .dlist,
.doc .ulist .olist,
.doc .ulist .ulist {
  margin-top: 0.5rem;
}

.doc .olist li + li,
.doc .ulist li + li {
  margin-top: 0.5rem;
}

.doc .ulist .listingblock,
.doc .olist .listingblock,
.doc .admonitionblock .listingblock {
  padding: 0;
}

.doc .admonitionblock .title,
.doc .exampleblock .title,
.doc .imageblock .title,
.doc .literalblock .title,
.doc .listingblock .title,
.doc .openblock .title,
.doc .videoblock .title,
.doc table.tableblock caption {
  color: var(--caption-font-color);
  font-family: var(--text-font-family), serif;
  font-size: calc(16 / var(--rem-base) * 1rem);
  font-style: var(--caption-font-style);
  font-weight: var(--caption-font-weight);
  hyphens: none;
  letter-spacing: 0.01em;
  padding-bottom: 0.075rem;
}

.doc table.tableblock caption {
  text-align: left;
}

.doc .olist .title,
.doc .ulist .title {
  font-style: var(--caption-font-style);
  font-weight: var(--caption-font-weight);
  margin-bottom: 0.25rem;
}

.doc .imageblock .title,
.doc .videoblock .title {
  margin-top: 0.5rem;
  padding-bottom: 0;
}

.doc details {
  margin-left: 1rem;
}

.doc details > summary {
  display: block;
  position: relative;
  line-height: var(--doc-line-height);
  margin-bottom: 0.5rem;
}

.doc details > summary::-webkit-details-marker {
  display: none;
}

.doc details > summary::before {
  content: "";
  border: solid transparent;
  border-left-color: currentColor;
  border-width: 0.3em 0 0.3em 0.5em;
  position: absolute;
  top: calc((var(--doc-line-height) * 0.5 - 0.3) * 1em);
  left: -1rem;
  transform: translateX(15%);
}

.doc details[open] > summary::before {
  border-color: currentColor transparent transparent;
  border-width: 0.5rem 0.3rem 0;
  transform: translateY(15%);
}

/*.doc details > summary::after {*/
/*  content: "";*/
/*  width: 1rem;*/
/*  height: 1em;*/
/*  position: absolute;*/
/*  top: calc((var(--doc-line-height) * 0.5 - 0.5) * 1em);*/
/*  left: -1rem;*/
/*}*/

.doc details.result {
  margin-top: 0.25rem;
}

.doc details.result > summary {
  color: var(--caption-font-color);
  font-style: italic;
  margin-bottom: 0;
}

.doc details.result > .content {
  margin-left: -1rem;
}

.doc .exampleblock > .content,
.doc details.result > .content {
  background: var(--example-background);
  border: var(--box-border);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 1em;
}

.doc .exampleblock > .content::after,
.doc details.result > .content::after {
  content: "";
  display: table;
  clear: both;
}

.doc .exampleblock > .content > :first-child,
.doc details > .content > :first-child {
  margin-top: 0;
}

.doc .exampleblock > .content > .stemblock:first-child div:first-child {
  padding-top: 0.25em !important;
}

.doc .sidebarblock {
  background: var(--sidebar-background);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 0.75rem 1rem;
}

.doc .sidebarblock.tip {
  border-left: var(--tip-color) 0.3rem solid;
}

.doc .sidebarblock.note {
  border-left: var(--note-color) 0.3rem solid;
}

.doc .sidebarblock.warning {
  border-left: var(--warning-color) 0.3rem solid;
}

.doc .sidebarblock.caution {
  border-left: var(--caution-color) 0.3rem solid;
}

.doc .sidebarblock.important {
  border-left: var(--important-color) 0.3rem solid;
}

.doc .sidebarblock > .content > .title {
  font-style: normal;
  font-size: calc(16 / var(--rem-base) * 1rem);
  font-weight: var(--body-font-weight-bold);
  line-height: 1.3;
  margin-bottom: 1rem;
}

.doc .sidebarblock.tip > .content > .title {
  color: var(--tip-color);
}

.doc .sidebarblock.note > .content > .title {
  color: var(--note-color);
}

.doc .sidebarblock.warning > .content > .title {
  color: var(--warning-color);
}

.doc .sidebarblock.caution > .content > .title {
  color: var(--caution-color);
}

.doc .sidebarblock.important > .content > .title {
  color: var(--important-color);
}

.doc .sidebarblock .title  {
  font-family: var(--body-font-family), sans-serif;
}

.doc .sidebarblock > .content > .title + *,
.doc .sidebarblock > .content > :not(.title):first-child {
  margin-top: 0;
}

.doc pre:has(> code) {
  background: var(--pre-background);
}

.doc pre > code,
.doc .listingblock pre:not(.highlight),
.doc .literalblock pre {
  background: var(--pre-background);
  display: block;
  overflow-x: auto;
  padding: 1em;
}

/* NEEDS REVIEW prevent pre in table from causing article to exceed bounds */
.doc .listingblock.wrap pre > code,
.doc .listingblock.wrap pre:not(.highlight),
.doc:not(.pdf-converter) table.tableblock pre > code,
.doc:not(.pdf-converter) table.tableblock pre:not(.highlight) {
  white-space: pre-wrap;
}

.doc.pdf-converter table.tableblock pre {
  font-size: calc(13.5 / var(--rem-base) * 1rem);
  max-width: 375px;
}

.doc .listingblock > .content {
  position: relative;
}

.doc .source-toolbox {
  display: flex;
  visibility: hidden;
  position: absolute;
  top: 0.25rem;
  right: 0.5rem;
  color: var(--pre-annotation-font-color);
  font-family: var(--body-font-family), sans-serif;
  font-size: calc(13 / var(--rem-base) * 1rem);
  line-height: 1;
  user-select: none;
  white-space: nowrap;
}

.doc .listingblock:hover .source-toolbox {
  visibility: visible;
}

.doc .source-toolbox .source-lang {
  text-transform: uppercase;
  letter-spacing: 0.075em;
}

.doc .source-toolbox > :not(:last-child)::after {
  content: "|";
  letter-spacing: 0;
  padding: 0 1ch;
}

.doc .source-toolbox .copy-button {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: none;
  border: none;
  color: inherit;
  outline: none;
  padding: 0;
  font-size: inherit;
  line-height: inherit;
  width: 1em;
  height: 1em;
}

.doc .source-toolbox .copy-icon {
  flex: none;
  width: inherit;
  height: inherit;
}

.doc .source-toolbox img.copy-icon {
  filter: var(--pre-annotation-icon-filter);
}

.doc .source-toolbox svg.copy-icon {
  fill: currentColor;
}

.doc .source-toolbox .copy-toast {
  flex: none;
  position: relative;
  display: inline-flex;
  justify-content: center;
  margin-top: 1em;
  background-color: var(--doc-font-color);
  border-radius: 0.25em;
  padding: 0.5em;
  color: var(--color-white);
  cursor: auto;
  opacity: 0;
  transition: opacity 0.5s ease 0.5s;
}

.doc .source-toolbox .copy-toast::after {
  content: "";
  position: absolute;
  top: 0;
  width: 1em;
  height: 1em;
  border: 0.55em solid transparent;
  border-left-color: var(--doc-font-color);
  transform: rotate(-90deg) translateX(50%) translateY(50%);
  transform-origin: left;
}

.doc .source-toolbox .copy-button.clicked .copy-toast {
  opacity: 1;
  transition: none;
}

.doc .language-console .hljs-meta {
  user-select: none;
}

.doc .dlist dt {
  font-style: italic;
}

.doc .dlist dd {
  margin: 0 0 0 1.5rem;
}

.doc .dlist dd + dt,
.doc .dlist dd > p:first-child {
  margin-top: 0.5rem;
}

.doc div.dlist dd:has(.exampleblock:last-child) {
  margin-bottom: 1em;
}

.doc td.hdlist1,
.doc td.hdlist2 {
  padding: 0.5rem 0 0.5em 0;
  vertical-align: top;
}

.doc tr:first-child > .hdlist1,
.doc tr:first-child > .hdlist2 {
  padding-top: 0;
}

.doc td.hdlist1 {
  font-weight: var(--body-font-weight-bold);
  padding-right: 0.25rem;
}

.doc td.hdlist2 {
  padding-left: 0.25rem;
}

.doc .colist {
  font-family: var(--text-font-family), serif;
  font-size: calc(16 / var(--rem-base) * 1rem);
  margin: 0.25rem 0 -0.25rem;
}

.doc .colist > table > tr > :first-child,
.doc .colist > table > tbody > tr > :first-child {
  padding: 0.25em 0.5rem 0;
  vertical-align: top;
}

.doc .colist > table > tr > :last-child,
.doc .colist > table > tbody > tr > :last-child {
  padding: 0.25rem 0;
}

.doc .conum[data-value] {
  /*border: 1px solid currentColor;*/
  border-radius: 100%;
  display: inline-block;
  font-family: var(--body-font-family), sans-serif;
  font-size: calc(13.5 / var(--rem-base) * 1rem);
  font-style: normal;
  line-height: 1.4;
  text-align: center;
  width: 1.5em;
  height: 1.5em;
  letter-spacing: -0.25ex;
  text-indent: -0.25ex;
  background-color: var(--color-jet-30);
  color: var(--color-white);
}

.doc .conum[data-value]::after {
  content: attr(data-value);
}

.doc .conum[data-value] + b {
  display: none;
}

.doc hr {
  border: solid var(--section-divider-color);
  border-width: 1px 0 0;
  height: 0;
}

.doc b.button {
  white-space: nowrap; /* effectively ignores hyphens setting */
}

.doc b.button::before {
  content: "[";
  padding-right: 0.25em;
}

.doc b.button::after {
  content: "]";
  padding-left: 0.25em;
}

.doc kbd {
  display: inline-block;
  font-size: calc(14 / var(--rem-base) * 1rem);
  background: var(--kbd-background);
  border: 1px solid var(--kbd-border-color);
  border-radius: 0.25em;
  box-shadow: 0 1px 0 var(--kbd-border-color), 0 0 0 0.1em var(--body-background) inset;
  padding: 0.25em 0.5em;
  vertical-align: text-bottom;
  white-space: nowrap; /* effectively ignores hyphens setting */
}

.doc kbd,
.doc .keyseq {
  line-height: 1;
}

.doc .keyseq {
  font-size: calc(16 / var(--rem-base) * 1rem);
}

.doc .keyseq kbd {
  margin: 0 0.125em;
}

.doc .keyseq kbd:first-child {
  margin-left: 0;
}

.doc .keyseq kbd:last-child {
  margin-right: 0;
}

.doc .menuseq,
.doc .path {
  hyphens: none;
}

.doc .menuseq i.caret {
  background-image: url(../img/octicons-16.svg#view-chevron-right);
  margin: 0 -0.25em;
}

.doc .menuseq i.fa {
  height: 1em;
  width: 1em;
  vertical-align: -0.125em;
}

.doc .menuseq i.fa-angle-right::before {
  content: "";
}

.doc .tag {
  user-select: none;
  -webkit-user-select: none; /* Safari */
  /*display: inline-block;*/
  font-family: var(--body-font-family), sans-serif;
  font-weight: 500;
  font-size: calc(12 / var(--rem-base) * 1rem);
  border: 1px solid var(--kbd-border-color);
  border-radius: 0.5em;
  padding: 0.25em 0.5em 0.25em 0;
  margin-right: 0.2em;
  vertical-align: middle;
  position: relative;
  bottom: 0.1em;
  white-space: nowrap; /* effectively ignores hyphens setting */
  background-color: var(--kbd-background);
}

.doc .tag::before {
  content: "";
  display: inline-block;
  width: 1.5em;
  height: 1.5em;
  position: relative;
  top: 0.55em;
  left: 0.2em;
  margin-right: 0.1em;
  background: url(../img/octicons-16.svg#view-tag) 0 0 /0.8rem no-repeat;
  filter: invert(50%);
  /*transform: scaleX(-1);*/
}

.doc .tag[data-tag-color="tip"]::before {
  filter: invert(63%) sepia(68%) saturate(562%) hue-rotate(61deg) brightness(84%) contrast(88%);  /* #43B929 */
}

.doc .tag[data-tag-color="note"]::before {
  filter: invert(65%) sepia(90%) saturate(4309%) hue-rotate(191deg) brightness(84%) contrast(96%);  /* #2D7DD2 */
}

.doc .tag[data-tag-color="caution"]::before {
  filter: invert(20%) sepia(92%) saturate(1600%) hue-rotate(270deg) brightness(90%) contrast(102%);  /* #802392 */
}

.doc .tag[data-tag-color="warning"]::before {
  filter: invert(79%) sepia(67%) saturate(6988%) hue-rotate(2deg) brightness(104%) contrast(106%);  /* #FF7700 */
}

.doc .tag[data-tag-color="important"]::before {
  filter: invert(14%) sepia(100%) saturate(7454%) hue-rotate(336deg) brightness(87%) contrast(107%);  /* #E40046 */
}

.doc :not(pre).nowrap {
  white-space: nowrap;
}

.doc .nobreak {
  hyphens: none;
  word-wrap: normal;
}

.doc :not(pre).pre-wrap {
  white-space: pre-wrap;
}

#footnotes {
  font-size: 0.85em;
  line-height: 1.5;
  margin: 2rem -0.5rem 0;
}

.doc td.tableblock > .content #footnotes {
  margin: 2rem 0 0;
}

.doc span.icon {
  text-align: center;
}

.doc .gradient {
  background-image: linear-gradient(20deg, #09009f, #E743D9);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.doc .gradient_tn {
  background-image: linear-gradient(20deg, var(--tip-color), var(--note-color));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.doc .gradient_tw {
  background-image: linear-gradient(20deg, var(--tip-color), var(--warning-color));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.doc .gradient_wi {
  background-image: linear-gradient(20deg, var(--warning-color), var(--important-color));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.doc .gradient_ni {
  background-image: linear-gradient(20deg, var(--note-color), var(--important-color));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.doc .gradient_nw {
  background-image: linear-gradient(20deg, var(--note-color), var(--warning-color));
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

#footnotes hr {
  border-top-width: 1px;
  margin-top: 0;
  width: 20%;
}

#footnotes .footnote {
  margin: 0.5em 0 0 1em;
}

#footnotes .footnote + .footnote {
  margin-top: 0.25em;
}

#footnotes .footnote > a:first-of-type {
  display: inline-block;
  margin-left: -2em;
  text-align: right;
  width: 1.5em;
}

